/***************************
 *	Variables
 **************************/
$font-family: "Open Sans", sans-serif;
$font-size: 1em;

$color-body: #e2e2e2;
$color-text: #282828;
$color-menu: #fcfcfc;

$variable-menu-animation-time: 1s;
$variable-burger-animation-time: 0.35s;

$variable-menu-animation-function: cubic-bezier(0.25, 0.1, 0.25, 1);
$variable-burger-animation-function: cubic-bezier(0.68, -0.55, 0.265, 1.55);

/***************************
 *	Project Main Styles
 **************************/
*,
*:before,
*:after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
.home-component {
  display: inline-block;
  justify-content: center;
  align-items: center;

  width:calc(100%);

  #root {
    display: flow-root !important ;
  }

  .mbsc-fr-inline {
    position: unset !important ;
  }

  body {
    font-family: $font-family;
    font-size: $font-size;
    background-color: $color-body;
  }

  /***************************
 *	Application Main Styles
 **************************/
  #menu--toggle[type="checkbox"]:not(:checked),
  #menu--toggle[type="checkbox"]:checked {
    display: none;
  }

  .menu--toggle__trigger,
  .menu--toggle__burger,
  .menu--toggle__burger:before,
  .menu--toggle__burger:after {
    position: absolute;
    top: 1.6em;
    left: 1.6em;
    width: 2em;
    height: 5px;
    background-color: $color-text;
    border-radius: 2px;
    cursor: pointer;
    z-index: 100;
    transition: $variable-burger-animation-time
      $variable-menu-animation-function;
  }

  .menu--toggle__trigger {
    height: 2em;
    background: none;
  }

  .menu--toggle__burger {
    &:before {
      content: "";
      top: 10px;
      left: 0;
    }
    &:after {
      content: "";
      top: 20px;
      left: 0;
    }
  }

  .menu__body {
    position: absolute;
    width: 7em;
    height: 7em;
    margin: 0;
    padding: 0;
    background-color: $color-menu;
    border-bottom-right-radius: 100%;
    box-shadow: 3px 3px 7px rgba($color-text, 0.2);
    z-index: 10;
    animation: checkboxUncheckedAnimation $variable-burger-animation-time both;
  }

  .menu__body-element,
  .menu__body-link {
    display: none;
    margin: 4.4em 0 -3.3em -0.1em;
    font-weight: normal;
    color: $color-text;
    text-decoration: none;
    text-transform: none;
    list-style: none;
    outline: 0;
  }

  .menu__body-element {
    text-indent: 2em;
  }

  .menu__body-link:hover {
    display: block;
    width: 0;
    color: $color-text;
    background-color: $color-body;
    animation: changeWidthOfElementAnimation $variable-burger-animation-time
      both;
  }

  #menu--toggle:checked + .menu--toggle__trigger {
    & + .menu--toggle__burger {
      top: 35px;
      transform: rotate(405deg);
      transition: transform $variable-burger-animation-time
        $variable-burger-animation-function;

      &:before {
        top: 0;
        transform: rotate(-90deg);
        transition: transform $variable-burger-animation-time
          $variable-burger-animation-function;
      }
      &:after {
        top: 0;
        transform: rotate(90deg);
        transition: transform $variable-burger-animation-time
          $variable-burger-animation-function;
      }
    }

    & + .menu--toggle__burger + .menu__body {
      animation: checkboxCheckedAnimation $variable-menu-animation-time
        $variable-menu-animation-function both;
    }

    & ~ .menu__body .menu__body-element,
    & ~ .menu__body .menu__body-link {
      display: block;
    }
  }

  .promotion {
    width: 100vw;
    height: 100vh;
    //background-image: url('http://upload.wikimedia.org/wikipedia/commons/thumb/1/18/Gooseberry-spring.jpg/1280px-Gooseberry-spring.jpg');
    background-size: cover;
  }

  /**
 *	Animations
 *
 *  @note checkboxCheckedAnimation - added fix for Microsoft Edge
 */
  @keyframes checkboxCheckedAnimation {
    50% {
      width: 100vw;
      height: 100vh;
    }
    100% {
      width: 100vw;
      height: 100vh;
      border-radius: 0;
    }
  }

  @keyframes checkboxUncheckedAnimation {
    0% {
      width: 100vw;
      height: 100vh;
    }
  }

  @keyframes changeWidthOfElementAnimation {
    50% {
      width: 0;
    }
    100% {
      width: 100%;
    }
  }

  #root {
    @media all and (max-width: 360px) and (height: 640px) {
      display: inline-grid;
      height: 640px;
      width: 360px;
    }

    @media all and (width: 411px) and (height: 731px) {
      display: inline-grid;
      height: 731px;
      width: 411px;
    }

    @media all and (width: 411px) and (height: 823px) {
      display: inline-grid;
      height: 823px;
      width: 411px;
    }

    @media all and (width: 320px) and (height: 568px) {
      display: inline-grid;
      height: 568px;
      width: 320px;
    }

    @media all and (width: 375px) and (height: 812px) {
      display: inline-grid;
      width: 375px;
      height: 812px;
    }

    @media all and (width: 375px) and (height: 667px) {
      width: 375px;
      height: 667px;
    }

    @media all and (width: 414px) and (height: 736px) {
      display: inline-grid;
      height: 736px;
      width: 414px;
    }

    @media all and (width: 375px) and (height: 667px) {
      display: inline-grid;
      height: 667px;
      width: 375px;
    }
  }
}
